    <el-button v-permission="'supplier:list'" :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button v-permission="'supplier:add'" type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
<!--    <el-button type="primary" :disabled="!hasSelectedRows">编辑</el-button>-->
    <el-button v-permission="'supplier:delete'" type="danger" :disabled="!hasSelectedRows"  @click="handleBatchDelete('/api/v1/admin/supplier/delete')">删除</el-button>
    <div class="table-statistics">
      <span class="stat-item stat-highlight">剩余预付=预付总额-可提余额；（每日结算：供应商群组内输入指令->供应商结算）</span>
    </div>

    <!-- 搜索按钮 -->
