              :inactive-value="0"
              @change="(value) => toggleSwitch('/api/v1/admin/supplier/prepay-check', row.id, Number(value))"
          />
        </template>
      </el-table-column>
      <el-table-column prop="prepayment_total" label="预付总额(元)" align="center" min-width="120">
        <template #default="{ row }">{{ (row.prepayment_total / 100).toFixed(2) }}</template>
      </el-table-column>
      <el-table-column prop="prepayment_remaining" label="剩余预付(元)" align="center" min-width="120">
        <template #default="{ row }">{{ (row.prepayment_remaining / 100).toFixed(2) }}</template>
      </el-table-column>
      <el-table-column prop="withdrawable_balance" label="可提余额(元)" align="center" min-width="120">
        <template #default="{ row }">{{ (row.withdrawable_balance / 100).toFixed(2) }}</template>
      </el-table-column>
      <el-table-column prop="today_receipt" label="今日收款(元)" align="center" min-width="120">
        <template #default="{ row }">{{ (row.today_receipt / 100).toFixed(2) }}</template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" align="center" min-width="150" show-overflow-tooltip/>
      <el-table-column prop="telegram_chat_id" label="供应商群组ID" align="center" min-width="150"/>
<!--      <el-table-column prop="created_at" label="创建时间" align="center" min-width="150"/>-->
<!--      <el-table-column prop="updated_at" label="更新时间" align="center" min-width="150"/>-->
      <el-table-column label="操作" align="center" fixed="right" min-width="200">
        <template #default="scope">
          <el-button v-permission="'supplier:edit'" size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button v-permission="'supplier:delete'" size="small" type="danger" @click="handleDelete('/api/v1/admin/supplier/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>




  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search, Close, Document } from '@element-plus/icons-vue';
import { ref, onMounted } from 'vue';


// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入编辑
import EditForm from './edit.vue';
// 导入编辑页面
const editFormRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log('准备打开编辑表单，数据:', row);
  
  if (editFormRef.value && typeof editFormRef.value.open === 'function') {
    editFormRef.value.open({ ...row}); // 传递 `row` 作为参数
  } else {
    console.error("EditForm 组件未正确挂载或open方法不存在！", {
      editFormRef: editFormRef.value,
      hasOpenMethod: editFormRef.value && typeof editFormRef.value.open === 'function'
    });
  }
};








// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/supplier");

// 搜索表单数据
const searchForm = ref({
  supplier_name: '',
  interface_code: '',
  status: ''
});

// 搜索加载状态
const searchLoading = ref(false);

// 处理搜索
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // 将搜索表单数据同步到searchParams
    searchParams.value = { ...searchForm.value };
    await refreshData();
  } catch (error) {
    console.error('搜索失败:', error);
    ElMessage.error('搜索失败，请重试');
  } finally {
    searchLoading.value = false;
  }
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    supplier_name: '',
    interface_code: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时加载数据
onMounted(() => {
  loadData();
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api,[row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string,) => {
  deleteRows(api);
};

</script>
